.NavBar {
    background-color: #FFD200 !important;
}

.logo {
    padding: 1em 0em 2.5em 0em;
}

.search-section {
    margin-top: 2.5em;
}

.search-box {
    width: 88% !important;
    margin-right: 0 !important;
    box-shadow: 3px 5px 20px rgba(0,0,0,0.15);
}

.search-button {
    width: 10%;
    margin: 0;
    background: red;
    border: red;
    position: absolute;
    right: 30px;
    height: 38px;
}

.search-button i {
    font-size: 40px !important;
    position: absolute;
    top: 0;
    left: 7px;
}

.TopNav .nav-link {
    display: inline;
    color: #555;
}
.TopNav .nav-link:hover {
    color: #7679ff;
}

.TopNav .nav-link i {
    margin-right: 5px;
    font-size: 14px;
}

.shopping-section {
    margin-top: 2em;
    text-align: left;
}

.shopping-section i {
    font-size: 55px;
    color: #717171;
    text-shadow: 2px 2px 2px rgba(0,0,0,0.25);
}

.shopping-section Button {
    font-weight: 200;
    color: #555;
    padding: 0em 0.75em;
}